export default "precision highp float;\n#define GLSLIFY 1\nuniform sampler2D map;\nvarying vec2 vUv;\nuniform float progress;\nuniform vec2 resolution;\nuniform bool isNext;\n#define M_PI 3.1415926535897932384626433832795\n\nvoid main() {\n    vec2 uv = vUv;\n    vec4 color = texture2D(map, uv);\n    \n    float y = gl_FragCoord.y / resolution.y; // [0, 1]\n\n    // https://codepen.io/ReGGae/pen/rNxpVEd?editors=0010\n    // float x = gl_FragCoord.x / resolution.x; // [0, 1]\n    // y -= ((sin(x * M_PI) * 1.) * .25);\n  \n    float opacity = step(progress, y);\n    if(isNext){\n        opacity = 1. - opacity;\n    }\n    gl_FragColor = vec4(\n        vec3( ( color.rgb ) / color.a ),\n        opacity\n    );\n    // Test scissor\n    // gl_FragColor = vec4(color);\n}\n";